.about {
  height: 100%;
  display: table-cell;
  vertical-align: middle;

  padding-top: 60px;
  padding-bottom: 60px;
  vertical-align: middle;
}
.wrapper {
  width: 70%;

  margin: auto;
  height: auto;
}
.title {
  font-weight: 300;
  margin: 0 0 10px;
  color: #22a39f;
  font-size: 54px;
  text-align: center;
}

.subTitle {
  font-size: 21px;
  text-align: center;
  color: #727878;
  margin: 0 0 10px;
  font-weight: 300;
  line-height: 1.4;
}

hr {
  margin: 40px 0px;
  border-color: #727878;
  opacity: 0.2;
  border-top: 1px solid #eee;
}
@media only screen and (max-width: 1250px) {
  .row {
    flex-direction: column;
  }
  .section-icon {
    width: 100% !important;
  }
  .section-skills {
    width: 100% !important;
  }
  .section-about {
    width: 100% !important;
  }
}

.icon-wrapper {
  width: 150px;
  height: 150px;
  background: #f3efe0;
  border: 8px solid #f3efe0;
  border-radius: 100px;
  overflow: hidden;
  margin: 0 auto;
  position: relative;
}
.icon {
  height: 320px;
  width: auto;
  position: relative;
  top: -60px;
  left: 0px;
  vertical-align: middle;
  filter: grayscale(60%);
}

.row {
  display: flex;
  width: 100%;
}
.section-icon {
  display: flex;
  justify-content: center;
  width: 20%;
  min-width: 160px;
}
.section-skills {
  width: 40%;
}
.section-about {
  width: 40%;
}
.section-heading {
  font-size: 28px;
  letter-spacing: 3px;
  margin-bottom: 10px;
  font-weight: 300;
  color: #22a39f;
  margin-top: 0;
}

.section-text {
  font-size: 18px;
  line-height: 1.64;
}

.skills {
  height: 340px;

  margin: auto;
  position: relative;
}

.lines {
  height: 101%;
  position: relative;
  padding: 0;
  list-style-type: none;
}

.line {
  height: inherit;
  width: 2px;
  position: absolute;
  background: rgba(238, 238, 238, 0.6);
}
.line.l--0 {
  left: 0;
}
.line.l--25 {
  left: 25%;
}
.line.l--50 {
  left: 50%;
}
.line.l--75 {
  left: 75%;
}
.line.l--100 {
  left: calc(100% - 1px);
}

.line__label {
  display: block;
  width: 100px;
  text-align: center;
  position: absolute;
  bottom: -20px;
  right: -50px;
}
.line__label.title {
  text-transform: uppercase;
  font-weight: bold;
}

.charts {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
}

.chart:not(:first-child) {
  margin-top: 10px;
}

.chart-ul {
  overflow: hidden;
  padding: 0;
  list-style-type: none;
  z-index: 0;
}

.chart__bar {
  height: 30px;
  margin-bottom: 10px;
  background: linear-gradient(to left, #4cb8c4, #3cd3ad);
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
  opacity: 0;
  animation: 1s anim-lightspeed-in ease forwards;
  z-index: 0;
}
.chart .chart__bar:nth-of-type(11) {
  animation-delay: 2.7s;
}
.chart .chart__bar:nth-of-type(10) {
  animation-delay: 2.5s;
}
.chart .chart__bar:nth-of-type(9) {
  animation-delay: 2.3s;
}
.chart .chart__bar:nth-of-type(8) {
  animation-delay: 2.1s;
}
.chart .chart__bar:nth-of-type(7) {
  animation-delay: 1.9s;
}
.chart .chart__bar:nth-of-type(6) {
  animation-delay: 1.7s;
}
.chart .chart__bar:nth-of-type(5) {
  animation-delay: 1.5s;
}
.chart .chart__bar:nth-of-type(4) {
  animation-delay: 1.3s;
}
.chart .chart__bar:nth-of-type(3) {
  animation-delay: 1.1s;
}
.chart .chart__bar:nth-of-type(2) {
  animation-delay: 0.9s;
}
.chart .chart__bar:nth-of-type(1) {
  animation-delay: 0.7s;
}

.chart__label {
  padding-left: 10px;
  line-height: 30px;
  color: white;
}

@keyframes anim-lightspeed-in {
  0% {
    transform: translateX(-200%);
    opacity: 1;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}
