


.page{
width:100vw;
height:100vh;

position: relative;
top:0;
background-color:#252934;
overflow:hidden;
}

.textContainer{

    position: absolute;
    top: 30%;
  
   
    width: 100%;

    z-index: 100;
}
.textContainer > h1 {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";   
    color: white;
    
    font-weight:100 !important;
    font-size: 3em;
    line-height: 67px; 
    letter-spacing: 6px;
    text-align: center;

}


